
.test-container {
  text-align: center;
  width: 100%;
  position: absolute;
  top: 60px;
  bottom: 0;
  overflow: auto;
  font-size: 14px;
}
.test-container .test-inner {
    position: absolute;
    overflow: hidden;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    max-width: 500px;
    min-width: 320px;
    margin: 0 auto;
}
.test-container .btn {
    position: absolute;
    top: 10px;
    line-height: 20px;
    border: 1px solid #ccc;
    border-radius: 10px;
    padding: 0 10px;
}
.test-container .btn.rule-btn {
      left: 10px;
}
.test-container .btn.prize-btn {
      right: 10px;
}
.test-container .prize-tip {
    padding: 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: absolute;
    top: 40px;
    left: 10px;
    right: 10px;
    height: 200px;
    background: rgba(0, 0, 0, 0.8);
    color: #ccc;
    z-index: 11;
}
.test-container .prize-tip h3 {
      line-height: 2;
      font-size: 25px;
}
.test-container .prize-tip p {
      line-height: 1.6;
      font-size: 20px;
}
.test-container .prize-content {
    position: absolute;
    width: 100%;
    background: rgba(0, 0, 0, 0.8);
    color: #ccc;
    z-index: 10;
    font-size: 16px;
    line-height: 1.8;
    padding: 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    top: 0;
    bottom: 0;
    overflow: auto;
}
.test-container .rule-content {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 20px;
    position: absolute;
    background: rgba(0, 0, 0, 0.8);
    color: #ccc;
    left: 20px;
    right: 20px;
    z-index: 9;
    bottom: 100px;
    line-height: 2;
}
.test-container .close {
    position: absolute;
    right: 10px;
    font-size: 40px;
    top: 0;
    line-height: 1;
}
.test-container .title {
    line-height: 5;
}
.test-container .test {
    display: inline-block;
    padding: 10px;
    width: 100px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: 100px;
    background: rgba(0, 0, 0, 0.2);
    vertical-align: top;
}
.test-container .test-1 {
    -webkit-transition: -webkit-transform .8s;
    transition: -webkit-transform .8s;
    transition: transform .8s;
    transition: transform .8s, -webkit-transform .8s;
}
.test-container .test-1.rotate45 {
      -webkit-transform: rotate(-45deg);
              transform: rotate(-45deg);
}
@-webkit-keyframes test2 {
50% {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}
}
@keyframes test2 {
50% {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}
}
.test-container .test-2 {
    -webkit-animation: test2 1s infinite;
            animation: test2 1s infinite;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
}
@-webkit-keyframes test3 {
50% {
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
}
}
@keyframes test3 {
50% {
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
}
}
.test-container .test-3 {
    -webkit-animation: test3 1s infinite;
            animation: test3 1s infinite;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
}
@-webkit-keyframes test4 {
50% {
    left: 0;
    -webkit-transform: translate(0, -50%);
            transform: translate(0, -50%);
    top: 50%;
}
100% {
    left: 100%;
    -webkit-transform: translate(-100%, -100%);
            transform: translate(-100%, -100%);
    top: 100%;
}
}
@keyframes test4 {
50% {
    left: 0;
    -webkit-transform: translate(0, -50%);
            transform: translate(0, -50%);
    top: 50%;
}
100% {
    left: 100%;
    -webkit-transform: translate(-100%, -100%);
            transform: translate(-100%, -100%);
    top: 100%;
}
}
.test-container .test-4 {
    position: absolute;
    top: 0;
    left: 100%;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    padding: 0;
    line-height: 30px;
    height: 30px;
    -webkit-animation: test4 4s forwards;
            animation: test4 4s forwards;
}
@-webkit-keyframes shake {
25% {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
}
50% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
75% {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
}
}
@keyframes shake {
25% {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
}
50% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
75% {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
}
}
@-webkit-keyframes shake1 {
25% {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
}
50% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
75% {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
}
}
@keyframes shake1 {
25% {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
}
50% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
75% {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
}
}
.test-container .test-5 {
    overflow: hidden;
    background: #fff;
    position: relative;
    z-index: -1;
}
.test-container .egg {
    display: inline-block;
    width: 25%;
    -webkit-transform-origin: bottom center;
            transform-origin: bottom center;
    vertical-align: bottom;
}
.test-container .egg.animation {
      -webkit-animation: shake 1s linear infinite;
              animation: shake 1s linear infinite;
}
.test-container .egg.break-animation {
      -webkit-animation: shake1 .2s linear infinite;
              animation: shake1 .2s linear infinite;
}
.test-container .egg img.egg-img {
      width: 100%;
      display: block;
}
.test-container .slide-top-enter-active,
  .test-container .slide-top-leave-active {
    -webkit-transition: -webkit-transform 1s;
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
}
.test-container .slide-top-enter,
  .test-container .slide-top-leave-to {
    -webkit-transform: translateY(200%);
            transform: translateY(200%);
}
.test-container .slide-left-enter-active,
  .test-container .slide-left-leave-active {
    -webkit-transition: -webkit-transform 1s;
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
}
.test-container .slide-left-enter,
  .test-container .slide-left-leave-to {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}
.test-container .slide-bottom-enter-active,
  .test-container .slide-bottom-leave-active {
    -webkit-transition: -webkit-transform .5s;
    transition: -webkit-transform .5s;
    transition: transform .5s;
    transition: transform .5s, -webkit-transform .5s;
}
.test-container .slide-bottom-enter,
  .test-container .slide-bottom-leave-to {
    -webkit-transform: translateY(-200%);
            transform: translateY(-200%);
}



