
.nav {
  padding: 10px;
  border-bottom: 1px solid #ccc;
  white-space: nowrap;
}
.nav > li {
    display: inline-block;
    margin: 5px;
}
.nav .tag {
    display: inline-block;
    padding: 0 20px;
    border: 1px solid;
    white-space: nowrap;
    color: #a9a9a9;
    border-radius: 14px;
    font-size: 14px;
    line-height: 28px;
}
.nav .tag:hover {
      border-color: orange;
}
.nav .tag.router-link-active {
      color: orange;
}
.nav .tag:hover {
      text-decoration: underline;
}



