
.rotary-table {
  overflow: hidden;
  max-width: 420px;
  min-width: 280px;
  margin: 0 auto;
  padding: 0 20px;
}
.rotary-table .title {
    text-align: center;
    margin: 20px;
}
.rotary-table img {
    display: block;
}
.rotary-table .table-container {
    position: relative;
}
.rotary-table .table-container .table {
      width: 100%;
      position: relative;
      -webkit-transition: -webkit-transform 6s ease;
      transition: -webkit-transform 6s ease;
      transition: transform 6s ease;
      transition: transform 6s ease, -webkit-transform 6s ease;
      -webkit-transform-origin: center;
              transform-origin: center;
      overflow: hidden;
}
.rotary-table .table-container .table img {
        width: 100%;
}
.rotary-table .table-container .table .prize {
        width: 50%;
        position: absolute;
        top: 0;
        bottom: 50%;
        left: 25%;
        -webkit-transform-origin: bottom center;
                transform-origin: bottom center;
        text-align: center;
        font-size: 20px;
        color: #fff;
        padding-top: 10%;
}
.rotary-table .table-container .center-round {
      position: absolute;
      left: 50%;
      top: 50%;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
      width: 26%;
      cursor: pointer;
}
.rotary-table .table-container .center-round .round {
        width: 100%;
}
.rotary-table .table-container .center-round .pointer {
        position: absolute;
        width: 20%;
        left: 50%;
        bottom: 97%;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
}
.rotary-table .result {
    text-align: center;
    line-height: 3;
}



